<template>
  <div class="home">

    <headers/>

    <div class="welcome"></div>
    <div class="description">
      <p class="c a font welcome-f">Hello, friend!</p>
      <p class="c a font description-f">Welcome to my website. My Git repositories are stored here.<br>You can use all my repositories in your projects.</p>
    </div>


  <div class="all-repositories">
    <div class="repository" v-for="item in request_data" :key="item">
