      <div><p class="a repository-description" >{{ item["description"] }}</p></div>
      <div class="shower">
        <div><p class="a repository-description license additional" v-if="item['license'] != undefined">{{ item['license'].name }} defending this project</p></div>
        <div><p class="a repository-description additional" v-if="item['created_at'] != undefined || item['updated_at'] != undefined">I am created at <span class="a">{{ (item["created_at"]).split("T")[0].replace('-', '/').replace('-', '/') }}</span> and last update it at <span class="a">{{ (item["updated_at"]).split("T")[0].replace('-', '/').replace('-', '/') }}</span></p></div>
        <div><p class="a repository-description additional" v-if="item['language'] != undefined"><span class="a langb" :id="item['language'].replace('+', 'p').replace('+', 'p')">{{ item["language"] }}</span> used as main language</p></div>
