  </div>

  </div>
</template>

<script>
import headers from '../components/headers.vue'
import helloiam from '../components/helloiam.vue'
import axios from 'axios'

export default {
  name: 'Home',
  components: { headers, helloiam },
  data() { 
    return {
      request_data: [],
      error_axios: ""
    }
  },
  mounted() {
    document.title = "Gleb Rodukov | Welcome"
    this.github_repository()
    document.addEventListener("DOMContentLoaded", () => { 
        const style = document.createElement('style');
        style.innerHTML = `
          body { transition: 0.3s; }
            `;
        document.head.appendChild(style);
    })
  },
  methods: {
    async github_repository() {
      await axios
        .get('https://api.github.com/users/rodukov/repos')
        .then(response => (this.request_data = response["data"].reverse()))
        .catch(function (error) { this.error_axios = error.status })
        console.log(this.error_axios)
    },
    sleep(ms) { return new Promise(resolve => setTimeout(resolve, ms)); }
  }
}
</script>

