<script>
export default {
    name: 'headers',
    props: {
        color_of_header_item1: "",
        color_of_background1: "",
        header_shadow1: "",
        header_enabled: false
    },
    data() {
        return {
            color_of_header_item: "",
            color_of_background: "",
            color_of_border: "",
            header_shadow: ""
        }
    },
    mounted() {
        /* Default theme */
        this.set_theme_h('light-theme');
        if (this.get_mobile() == false) {
            document.getElementById('panelclose').style.display = 'none';
        }

    },
    methods: {
        set_theme_h(theme_name) {
            document.documentElement.className = theme_name;
            this.color_of_header_item = getComputedStyle(document.documentElement).getPropertyValue('--color-dfont')
            this.color_of_background = getComputedStyle(document.documentElement).getPropertyValue('--color-background')
            this.header_shadow = getComputedStyle(document.documentElement).getPropertyValue('--header-shadow')
            this.color_of_border = getComputedStyle(document.documentElement).getPropertyValue('--color-of-border')
        },
        get_mobile: () => ((window.innerWidth > 0) ? window.innerWidth : screen.width) <= 855,
        show_header() { document.getElementById("header").style.top = '0' },
        hide_header: () => document.getElementById("header").style.top = '-150%',
        dropdown_logic: (id) => (document.getElementById(id).style.display == 'none' || document.getElementById(id).style.display == '') ? document.getElementById(id).style.display = 'block' : document.getElementById(id).style.display = 'none',
    }
}
</script>


